










































































import {
	mapState
} from 'vuex'
import API from '@/util/api'
import amap from '@/common/amap-wx.js';
import {
	getReqest
} from '@/util/network.js'
export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			items: [1, 2, 3, 3, 5, 6, 6, 7],

			amapPlugin: null,
			key: '72239a17febe0f534f11c5b1fbd8ce4c',
			addressName: '获取中...',
			banners: []
		}
	},
	methods: {
		async getBanner() {
			const params = {
				latitude: '20.111111',
				longitude: '113.09091'
			}
			const that = this;
			getReqest('banner/list', params, function(res) {
				that.banners = res;
				console.log('res ###', res);
			})
		},
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		},
		goSearch() {
			uni.navigateTo({
				url: "../HM-search/HM-search"
			})
		},
		goShop() {
			uni.navigateTo({
				url: "shop/shop"
			})
		},
		getRegeo() {
			var that = this;
			uni.showLoading({
				title: '获取信息中'
			});
			this.amapPlugin.getRegeo({
				success: (data) => {
					console.log(data)
					that.addressName = data[0].name;
					console.log("addressName ", that.addressName)

					uni.hideLoading();
				},
				fail: e => {
					uni.hideLoading();

					console.log(e)
				}
			});
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	onLoad() {
		this.amapPlugin = new amap.AMapWX({
			key: this.key
		});
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: (res) => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		}
		this.getRegeo();
		this.getBanner();
	}
}
